.hero {
  position: relative;
  margin-bottom: 34px;
}
.hero::before {
  content: "";
  position: absolute;
  min-height: 302px;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), image-set(url("../img/hero-background-image.png") 1x, url("../img/hero-background-image@2x.png") 2x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
@media (min-width: 768px) {
  .hero::before {
    display: none;
  }
}
@media (min-width: 1440px) {
  .hero::before {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), image-set(url("../img/hero-background-image-desktop.png") 1x, url("../img/hero-background-image-desktop@2x.png") 2x);
    display: block;
    background-size: cover;
    background-position: left;
    left: 50%;
    right: 0;
    top: -128px;
    bottom: 0;
    height: 694px;
  }
}
@media (min-width: 320px) {
  .hero {
    min-height: 480px;
  }
}
@media (min-width: 768px) {
  .hero {
    min-height: 875px;
    margin-bottom: 0;
  }
  .hero .hero__container {
    display: grid;
  }
  .hero .hero__image {
    order: 2;
    position: absolute;
    top: 252px;
    left: 31px;
    z-index: -1;
  }
}
@media (min-width: 1440px) {
  .hero {
    min-height: 580px;
    margin-bottom: 85px;
  }
  .hero .hero__container {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "title image" "description image" "button image";
    grid-template-rows: min-content min-content 1fr;
  }
  .hero .hero__image {
    position: static;
    grid-area: image;
    margin-left: -385px;
    margin-top: -11px;
  }
}

